<template>
  <div>
    <div class="container flex-body">
      <div class="flex-content">
        <Navbar></Navbar>
        <div class="banner row border-bottom pt-5 pb-5">
          <h2 class="font-weight-bolder text-center w-100">FAQ</h2>
        </div>
        <div class="card my-5 text-center">
          <div class="card-header">
            <ul class="nav nav-tabs card-header-tabs">
              <li class="nav-item">
                <a
                  class="nav-link"
                  :class="{ active: whichIs === '' }"
                  href="#"
                  @click.prevent="pickQ('')"
                  ></a
                >
              </li>
              <li class="nav-item">
                <a
                  class="nav-link"
                  :class="{ active: whichIs === '' }"
                  href="#"
                  @click.prevent="pickQ('')"
                  ></a
                >
              </li>
              <li class="nav-item">
                <a
                  class="nav-link"
                  :class="{ active: whichIs === '' }"
                  href="#"
                  @click.prevent="pickQ('')"
                  ></a
                >
              </li>
              <li class="nav-item">
                <a
                  class="nav-link"
                  :class="{ active: whichIs === '' }"
                  href="#"
                  @click.prevent="pickQ('')"
                  >
                </a>
              </li>
            </ul>
          </div>
          <div class="card-body p-0">
            <div class="accordion w-100 ml-auto" id="accordion">
              <div class="card" v-for="(item, index) in faqFilter" :key="index">
                <div class="card-header bg-white">
                  <button
                    class="btn collapsed p-0"
                    type="button"
                    @click="toggleCollapse(item, index)"
                    data-toggle="collapse" :data-target="item.id"
                  >
                    <ul class="list-unstyled mb-0">
                      <li>{{ "Q" + (index + 1) + ". " + item.question }}</li>
                    </ul>
                  </button>
                </div>
                <div :id="item.id" class="collapse" :key="index" data-parent="#accordion">
                  <div class="card-body bg-primary text-white">
                    <ul class="list-unstyled mb-0">
                      <li>{{ "A" + (index + 1) + ". " + item.answer }}</li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="flex-footer"><Footer></Footer></div>
    </div>
  </div>
</template>
<script>
import Navbar from "./Navbar";
import Footer from "./Footer";

export default {
  components: {
    Navbar,
    Footer
  },
  data() {
    return {
      whichIs: "",
      items: [
        {
          category: "",
          question: "",
          answer: "(VISAMastercardJCB)",
          id:"collapseA"
        },
        {
          category: "",
          question: "",
          answer: "",
          id:"collapseB"
        },
        {
          category: "",
          question: "",
          answer: "",
          id:"collapseC"
        },
        {
          category: "",
          question: "",
          answer: "",
          id:"collapseD"
        },
        {
          category: "",
          question: "",
          answer:
            "",
          id:"collapseE"
        },
        {
          category: "",
          question: " ",
          answer:
            "13:00 18:00 ",
          id:"collapseF"
        }
      ]
    };
  },
  methods: {
    pickQ(Q) {
      const vm = this;
      vm.whichIs = Q;
    },
    toggleCollapse(item, index) {
      $(`#${item.id}`).collapse('toggle')
    }
  },
  computed: {
    faqFilter() {
      const vm = this;
      let pay = [];
      let order = [];
      let send = [];
      if (vm.whichIs === "") {
        pay = vm.items.filter(item => {
          return item.category === "";
        });
        return pay;
      } else if (vm.whichIs === "") {
        order = vm.items.filter(item => {
          return item.category === "";
        });
        return order;
      } else if (vm.whichIs === "") {
        send = vm.items.filter(item => {
          return item.category === "";
        });
        return send;
      } else if (vm.whichIs === "") {
        return vm.items;
      }
    }
  }
};
</script>


